@import url(https://fonts.googleapis.com/css?family=Crimson+Text:400,400i|Raleway:400,700);
:root, body, #root {
  margin: 0;
  padding: 0;
  height: 100%;
  --sans-serif: 'Raleway';
  --serif: 'Crimson Text';
  --main-shadow: 0px 16px 23px -14px rgba(0,0,0,0.75);
  --side-shadow: 0px 16px 30px -18px rgba(0,0,0,0.75);
  --slight-shadow: 0px 3px 12px -3px rgba(94,94,94,1);
  --slight-shadow-inset: 0px 0px 12px -3px rgba(94,94,94,1) inset;

  --neutral-color: black;
  --neutral-bg-color: white;

  --success-color: #155724;
  --sucecess-bg-color: #d4edda;

  --danger-color: #721c24;
  --danger-bg-color: #f8d7da;

  --warning-color: #856404;
  --warning-bg-color: #fff3cd;


  font-size: 16px;
  font-family: var(--sans-serif), sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}
.logo {
  font-weight: 700;
  font-size: 4rem;
  position: relative;
}

.logo__link {
  text-decoration: none;
  color: black;
}

.logo::after {
  content: "";
  opacity: 0.5;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  position: absolute;
  z-index: -1;

  background-image: url(/static/media/logo.67bab102.png);
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  margin: -1rem -0.5rem;
  padding: 1rem 0.5rem;
}
.heading {
  font-family: var(--sans-serif), sans-serif;
  font-size: 2rem;
  margin: 0.25rem 0;
}
.secondary-heading {
